import axios from 'axios'; // axios
import QS from 'qs'; // qspost
import {
  Toast,
  MessageBox
} from 'mint-ui'; //
import router from '../router/index.js'
//axiosaxios
var instance = axios.create({
  timeout: 3000
});

const analog = {
  substring: '/api', // 
  relpaceAdd: 'http://iyouupload.198ty.com' //
}

let urlObj = /^\/videos/;
//
instance.interceptors.request.use(
  config => {
    if (config.method === 'post') {
      config.data = QS.stringify(config.data);
    };
    // ... loading 
    //isShowLoading(true);
    // token
    // tokencros
    let TOKEN = sessionStorage.getItem('tata_token');
    let HEADERIMG = sessionStorage.getItem('tata_headimg');
    let TATAMD5 = sessionStorage.getItem('tata_md5');
    
    /*if(HEADERIMG) {
      config.headers['Content-Type'] = 'multipart/form-data';
      config.headers['fileType'] = '1';
      config.headers['pathType'] = '1';
    }*/

    if(TOKEN) {
      //  token
      config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
      config.headers['Authorization'] = TOKEN;
    } 

    if(TATAMD5) {
      //  token
      config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
      config.headers['sign'] = TATAMD5;
      config.headers['Authorization'] = TOKEN;
    } 

    if (!urlObj.test(config.url)) {
      config.baseURL = analog.substring
      if (process.env.NODE_ENV == 'production') {
        config.baseURL = ''
      }
    }

    return config;
  },
  error => {
    // 
    console.warn(error);
    return Promise.reject(error);
  }
);

//
instance.interceptors.response.use(
  function (response) {
    // 
    //isShowLoading(false);
    // token
    // token
    // 
    const {
      status
    } = response.data;
    // 401
    if (Object.is(status, 401)) {
      // token
      // 1.token
      // 2. 
      //  todo...
    }
    return response;
  },
  function (error) {
    // 
    //isShowLoading(false);
    return Promise.reject(error);
  }
);

export function get(url, params){
  return new Promise((resolve, reject) =>{
      instance.get(url, {
          params: params
      }).then(res => {
          resolve&&resolve(res.data);
      }).catch(err =>{
          reject&&reject(err.data)
      })
  });
}

export function post(url, params) {
  return new Promise((resolve, reject) => {
      instance.post(url, params)
      .then(res => {
          resolve&&resolve(res.data);
      })
      .catch(err =>{
          reject&&reject(err.data)
      })
  });
}

class http {
  static async get(url, params) {
    console.log(params)
    return await instance.get(url, {
      params
    })
  }
  static async post(url, params) {
    console.log(params)
    return await instance.post(url, params);
  }
}

export default http;



